<template>
  <div class="prefer full-screen">
    <div class="page-panel">
      <pem-scroll-view ref="scroll" class="scroll-view">
        <div>
          <div class="auth-investor-wrapper">
            <div class="auth-invertor-top">
              <span class="auth-investor-title">{{purpose === 'auth' ? '设置' : '修改'}}投资偏好</span>
              <span v-if="purpose === 'auth'" class="auth-investor-step">（2 / 2）</span>
            </div>
          </div>
          <div class="hobby-wrapper">
            <div class="hobby-title">单笔投资额度</div>
            <div class="hobby-quota">
              <input type="number" v-model="minfund" name="minfund" placeholder="最低融资额">
              <span>至</span>
              <input type="number" v-model="maxfund" name="maxfund" placeholder="最高融资额">
              <span>万</span>
              <img v-if="currencyzj == 100" @click="switchCurrencyType('200')" src="../../../common/image/minisite/auth/icon-setting-rmb@2x.png" alt="">
              <img v-if="currencyzj == 200" @click="switchCurrencyType('100')" src="../../../common/image/minisite/auth/icon-setting-dollar@2x.png" alt="">
            </div>
          </div>
          <div class="hobby-wrapper">
            <div class="hobby-title">关注投资领域</div>
            <div class="hobby-btns">
              <div 
                class="hobby-btn field-btn"
                @click="selectItem('field', item.value)"
                :class="{'active': changeMyfieldsList.indexOf(item.value) > -1}"
                v-for="item in myfieldsList"
                :key="item.value">{{ item.name }}</div>
            </div>
          </div>
          <div class="hobby-wrapper">
            <div class="hobby-title">关注轮次</div>
            <div class="hobby-btns">
              <div 
                class="hobby-btn turn-btn"
                @click="selectItem('turn', item.value)"
                :class="{'active': changeMyturnsList.indexOf(item.value) > -1}"
                v-for="item in myturnsList"
                :key="item.value">{{ item.name }}</div>
            </div>
          </div>
          <div class="btn-wrapper"><pem-button :text="purpose === 'auth' ? '提交认证' : '确定'" @submit="submit"/></div>
        </div>
      </pem-scroll-view>
      <div class="elect-button">
        <pem-button text="保存" @submit="submit" class="submit" />
      </div>
    </div>
    <pem-toast ref="toast" :text="toastText"></pem-toast>
  </div>
</template>

<script type="text/ecmascript-6">
import PemButton from 'components/pem-button/pem-button'
import PemScrollView from 'components/scroll-view/scroll-view'
import PemToast from 'components/pem-toast/pem-toast'
import { mapState } from 'vuex'
import { MiniSiteApi } from 'api'
import { setTitle } from 'common/js/util'

export default {
  components: {
    PemScrollView,
    PemButton,
    PemToast
  },
  data () {
    return {
      purpose: 'auth',
      toastText: '',
      minfund: null,
      maxfund: null,
      currencyzj: '100',
      changeMyfieldsList: [],
      changeMyturnsList: [],
      myfieldsList: [],
      myturnsList: [],
      timer: null
    }
  },
  created () {
    this._initWXShare()
    this._setTitle('项目工场-投资偏好')
    setTimeout(() => {
      this._initData()
    }, 20)
  },
  computed: mapState({auth: state => state.auth}),
  methods: {
    _initWXShare () {
      this.wxShare({
        title: '为您推荐项目工场',
        content: '我正在使用项目工场，有料的BP、有趣的VC都在这儿，一起来看项目吧！',
        linkUrl: window.location.origin
      })
    },
    _setTitle (title) {
      setTitle(title)
    },
    _initData () {
      this.purpose = this.$route.query.purpose || 'auth'
      this.timer = null
      this.findBaseDataInfo(() => {
        if (this.purpose === 'change') {
          this.initMyPrefer()
        }
      })
    },
    switchCurrencyType (type) {
      this.currencyzj = type
    },
    submit () {
      if (this._validate()) {
        this._updateMyPrefer({
          currencyzj: this.currencyzj.toString(),
          minfund: this.minfund.toString(),
          maxfund: this.maxfund.toString(),
          myfieldsList: this.changeMyfieldsList,
          myphasesList: ['100'],
          myturnsList: this.changeMyturnsList
        }, data => {
          if (+data.resultcode === 1) {
            this.toast(this.purpose === 'auth' ? '提交成功' : '保存成功')
            this.timer = setTimeout(() => {
              if (this.purpose === 'auth') {
                this.$router.replace('/index')
              } else {
                this.$router.goBack()
              }
            }, 1000)
          }
        })
      }
    },
    _validate () {
      if (!this.minfund) {
        this.toast('请输入最小融资金额')
        return false
      }
      if (!/^[1-9]\d{1,7}$/.test(+this.minfund)) {
        this.toast('最低投资额不能小于2个字符<br/>最高投资额不能大于8个字符')
        return false
      }
      if (!this.maxfund) {
        this.toast('请输入最大融资金额')
        return false
      }
      if (!/^[1-9]\d{1,7}$/.test(+this.maxfund)) {
        this.toast('最低投资额不能小于2个字符<br/>最高投资额不能大于8个字符')
        return false
      }
      if (+this.minfund > +this.maxfund) {
        this.toast('投资额度最小额度不能大于最大额度')
        return false
      }
      if (this.changeMyfieldsList.length < 5) {
        this.toast('至少选择5个关注领域')
        return false
      }
      if (this.changeMyfieldsList.length > 10) {
        this.toast('最多选择 10 个领域')
        return false
      }
      if (this.changeMyturnsList.length === 0) {
        this.toast('请选择关注的轮次')
        return false
      }
      return true
    },
    selectItem (type, name) {
      if (type === 'field') {
        let index = this.changeMyfieldsList.indexOf(name)
        if (index === -1 && this.changeMyfieldsList.length < 10) {
          this.changeMyfieldsList.push(name)
        } else if (index === -1 && this.changeMyfieldsList.length >= 10) {
          this.toast('最多选择 10 个领域')
        } else {
          this.changeMyfieldsList.splice(index, 1)
        }
      }
      if (type === 'turn') {
        let index = this.changeMyturnsList.indexOf(name)
        if (index === -1) {
          this.changeMyturnsList.push(name)
        } else {
          this.changeMyturnsList.splice(index, 1)
        }
      }
    },
    toast (text) {
      this.toastText = text
      this.$refs.toast.show()
    },
    findBaseDataInfo (callback) {
      this._findBaseDataInfo(data => {
        if (+data.resultcode === 1) {
          this.myfieldsList = data.fieldList || []
          this.myturnsList = data.trunList || []
          // 默认选中第一个领域,轮次 (认证的时候)
          if (this.purpose === 'auth') {
            this.changeMyfieldsList.push(this.myfieldsList[0]['value'])
            this.changeMyturnsList.push(this.myturnsList[0]['value'])
          }
          callback && callback()
        }
      })
    },
    initMyPrefer () {
      this._initMyPrefer(data => {
        if (+data.resultcode === 1) {
          this.minfund = data.minfund || null
          this.maxfund = data.maxfund || null
          this.currencyzj = data.currencyzj || '100'
          this.changeMyfieldsList = (data.myfieldsList || []).map(item => { return item.value })
          this.changeMyturnsList = (data.myturnsList || []).map(item => { return item.value })
        }
      })
    },
    _findBaseDataInfo (callback) {
      MiniSiteApi.findBaseDataInfo().then(data => {
        callback(data)
      })
    },
    _updateMyPrefer (options, callback) {
      MiniSiteApi.updateMyPrefer(options).then(data => {
        callback(data)
      })
    },
    _initMyPrefer (callback) {
      MiniSiteApi.initMyPrefer().then(data => {
        callback(data)
      })
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
@import "~common/stylus/variable"
@import "~common/stylus/mixin"
.prefer
  font-size: 0.24rem
  .auth-investor-wrapper
    padding: 0 0.3rem
  .auth-invertor-top
    font-size: 0
    padding: 0.44rem 0 0.32rem
    .auth-investor-title
      height: 0.6rem
      line-height: 0.6rem
      font-size: 0.44rem
      font-weight: bold
      color: #41403E
    .auth-investor-step
      font-size: 0.24rem
      color: #A7A5A3
      padding-left: 0.2rem
  .hobby-wrapper
    padding-bottom: 0.4rem
    .hobby-title
      line-height: 0.36rem
      font-size: 0.32rem
      color: #41403E
      padding: 0 0 0.3rem 0.3rem
    .hobby-quota
      font-size: 0
      padding: 0 0.3rem
      overflow: hidden
      span, img, input
        display: inline-block
        text-align: center
        vertical-align: middle
        font-size: 0.26rem
        color: #2F3E4D
      input
        width: 1.52rem
        height: 0.56rem
        background: #F0F0F0
        border-radius: 0.04rem
      span
        margin: 0 0.2rem
      img
        width: 1.08rem
        height: 0.5rem
        float: right
    .hobby-btns
      font-size: 0
      .hobby-btn
        display: inline-block
        height: 0.56rem
        line-height: 0.56rem
        margin: 0 0 0.2rem 0.2rem
        color: #A7A5A3
        text-align: center
        font-size: 0.24rem
        background: #FFFFFF
        border-radius: 0.08rem
        border: 0.02rem solid #E5E5E5
        &.active
          color: #41403E
          background: #FFF6E2
          border-color: #DD9900
      .field-btn
        width: 1.6rem
      .turn-btn
        width: 1.24rem
  .btn-wrapper
    padding: 0.4rem 0 0.8rem
    .pem-submit
      width: 6.1rem
      height: 0.88rem
      line-height: 0.88rem
      margin: 0 auto
</style>



